import Service from "./service"
import { IImage } from "../interfaces"

class ImageService {
	private api = new Service()

	public list<T>(type?: string): Promise<T> {
		const param = type ? `?type=${type}` : ""
		return this.api.get("image" + param)
	}

	public delete(id: number): Promise<void> {
		return this.api.delete(`image/${id}`)
	}

	public create({ imageString, type, userId = "0" }: IImage) {
		return this.api.post("image", {
			imageString,
			type,
			authorId: userId,
		})
	}

	public update(image: IImage) {
		return this.api.put<IImage>(`image/${image.id}`, image)
	}
}

export default ImageService
