import React from "react"
import { ButtonPartner, Container, Description, ImageWraper } from "./styles"
import { Link } from "gatsby"
import { useTranslation } from "react-i18next"
import { translate } from "../../../../utils/translate"

export default function sectTwo() {
	const { t } = useTranslation()
	const partners = ["Docnix", "B2Finance", "Siemens"]

	const __renderPartnera = () => {
		return (
			<div className="row">
				{partners.map(item => {
					return (
						<div key={item} className="col-sm">
							<ImageWraper className="col-sm">
								<img
									src={t(`Partners.${item}.imageUrl`)}
									width={"90%"}
									height={250}
									alt={`Logo ${t(`Partners.${item}.imageUrl`)}`}
									className="align-middle"
								/>
							</ImageWraper>

							<div
								className="row"
								style={{
									position: "relative",
								}}
							>
								<div className="col-sm" style={{ fontSize: 20, height: 100 }}>
									<p>{t(`Partners.${item}.text`)}</p>
								</div>
							</div>
						</div>
					)
				})}
			</div>
		)
	}

	return (
		<div>
			<section>
				<Container>
					<Description>
						<div className="container">
							<h2>{t(`Partners.PageName`)}</h2>
							<hr />
							<div className="row">
								<div className="col-sm" style={{ padding: 5 }}>
									<p style={{ fontSize: 30, fontWeight: 500 }}>
										<i>{t("Partners.SubTitle")}</i>
									</p>
									{/* */}
								</div>
							</div>
							{__renderPartnera()}
							<Link to="validacoes-empresa">
								<div className="row">
									<div className="col-sm" style={{ padding: 5 }}>
										<a href="http://google.com.br" target="_blank">
											<ButtonPartner className="btn btn-cta" type="button">
												{t("Partners.Buttons.Transformation")}
											</ButtonPartner>
										</a>
									</div>
								</div>
							</Link>
						</div>
					</Description>
				</Container>
			</section>
		</div>
	)
}
