import React from "react"

import { Container, ButtonRedir, Description, Footer } from "./styles"
import Button from "../../../../utils/Button"

import { translate } from "../../../../utils/translate"
import { Link } from "gatsby"
import { FaChevronRight } from "react-icons/fa"

export default function sectThree() {
	return (
		<div className="container">
			<section id="about-us">
				<Description>
					<h3>{translate("Artia.title")}</h3>
					<Container>
						<div className="row">
							<div className="col-md-12 col-lg-4" style={{ paddingTop: 50 }}>
								<ul>
									<li>
										<FaChevronRight /> {translate("Artia.option1")}
									</li>
									<li>
										<FaChevronRight /> {translate("Artia.option2")}
									</li>
									<li>
										<FaChevronRight /> {translate("Artia.option3")}
									</li>
								</ul>
							</div>
							<div className="col-md-12 col-lg-8">
								<img
									src={
										"https://www.kivalitaconsulting.com/wp-content/artia.png"
									}
									width={"100%"}
								></img>
							</div>
						</div>
					</Container>
				</Description>
				<Footer>
					<p>{translate("Artia.footer")}</p>
				</Footer>
			</section>
		</div>
	)
}
