import React, { useState, useEffect } from "react"
import { Link } from "gatsby"
import { useTranslation } from "react-i18next"

import Button from "../../../../../../utils/Button"

export default function SlideWide({ name }) {
	const { t } = useTranslation()

	const divImageStyle = (image: string) => {
		return {
			backgroundImage: `url(${image})`,
			height: "500px",
			width: "100%",
			backgroundRepeat: "no-repeat",
			backgroundSize: "cover",
			paddingTop: "10%",
		}
	}

	return (
		<div
			className="d-block w-100"
			style={divImageStyle(t(`Slide.${name}.imageUrl`))}
		>
			<div className="slideText">
				<h3 style={{ textAlign: "center" }}>
					<strong>{t(`Slide.${name}.title`)}</strong>
				</h3>
				<p>{t(`Slide.${name}.text`)}</p>
				<Link to={t(`Slide.${name}.url`)}>
					<Button
						title={"Saiba Mais"}
						bgColor={"#f4bf8c"}
						fontFamily={"Poppins"}
						fontSize={17}
						fontWeight={600}
						textColor={"#FFF"}
						padding={"18px 40px 18px 40px"}
					/>
				</Link>
			</div>
		</div>
	)
}
