import styled from "styled-components"

export const Container = styled.div`
	background-color: #fff;
	width: 100%;
	color: #115964;
	font-family: "Montserrat", sans-serif;
	padding: 10px 0px;

	.row {
		align-items: center;
	}
`

export const Description = styled.div`
	margin-bottom: 15px;
	font-weight: 300;
	font-family: "Montserrat", sans-serif;
	font-size: 18px;
	text-align: center;
	@media (max-width: 576px) {
		text-align: center;
	}

	h2 {
		font-size: 35px;
		font-weight: 600;
		letter-spacing: 0ch;
		text-align: left;
	}

	h3 {
		font-size: 35px;
		font-weight: 600;
		letter-spacing: 0ch;
		text-align: left;
	}

	hr {
		border: 1px solid #f4bf8c;
		text-align: center;

		@media (max-width: 576px) {
			text-align: center;
			margin: 30px auto;
		}
	}
`

export const ContainerRedir = styled.div`
	img {
		width: 100%;
	}

	@media (max-width: 576px) {
		margin: 0 0 120px 0;
	}
`

export const ButtonRedir = styled.div`
	position: absolute;

	@media (min-width: 992px) {
		bottom: -8px;
		left: -8px;
	}

	@media (max-width: 576px) {
		width: 100%;
		left: 0;
		text-align: center;
		bottom: 16px;
	}
`
export const ButtonPartner = styled.button`
	background-color: #f4bf8c;
	color: white;
	font-family: Poppins, sans-serif;
	font-size: 17px;
	font-weight: 600;
	width: 100%;
`
