import styled from "styled-components"

export const Container = styled.div`
	margin-top: -30px;

	.carousel-inner {
		object-fit: cover;
		height: 500px;
	}

	.carousel-item {
		height: 500px;
	}

	li {
		background-color: #8888;
		border-radius: 50%;
		width: 8px;
		height: 8px;
	}

	.slideTextSmall {
		font-family: "Montserrat", sans-serif;
		font-size: 20px;
		color: #115964;
		font-weight: 500;
		background-color: #ffffffd4;
		letter-spacing: 1.4px;
		text-align: center;
		padding: 20px;
		border-radius: 10px;
	}
`
export const ContainerDescription = styled.div`
	margin: 30px 0px 0px 15px;
	font-family: "Montserrat", sans-serif;
	font-size: 16px;
	letter-spacing: 1.4px;
	color: #115964;
	font-weight: 600;

	hr {
		border: 1px solid #f4bf8c;
		width: 50px;
		margin: 30px 0px 40px 40%;
	}

	@media (max-width: 768px) {
		text-align: center;
	}
`
export const ContainerSubDescription = styled.div`
	font-family: "Montserrat", sans-serif;
	font-size: 22px;
	color: #115964;
	font-weight: 400;
`
