import React, { Fragment } from "react"
import { Container, Description } from "./styles"
import { useTranslation } from "react-i18next"
import Media from "react-media"

export default function Pain() {
	const { t } = useTranslation()

	return (
		<div>
			<section>
				<Container>
					<Description>
						<hr />
						<div className="container-fluid">
							<Media
								queries={{
									small: "(max-width: 599px)",
									medium: "(min-width: 600px) and (max-width: 1199px)",
									large: "(min-width: 1200px)",
								}}
							>
								{matches => (
									<Fragment>
										{matches.small && (
											<div
												style={{
													fontFamily: "Montserrat,sans-serif",
													fontSize: 30,
													color: "#115964",
													fontWeight: "bold",
													backgroundColor: "#ffffffc9",
													textAlign: "center",
												}}
											>
												Precisando diminuir retrabalhos custos e desperdícios no
												seu processo?
											</div>
										)}
										{matches.medium && (
											<div
												style={{
													fontFamily: "Montserrat,sans-serif",
													fontSize: 30,
													color: "#115964",
													fontWeight: "bold",
													backgroundColor: "#ffffffc9",
													textAlign: "center",
												}}
											>
												Precisando diminuir retrabalhos custos e desperdícios no
												seu processo?
											</div>
										)}
										{matches.large && (
											<div
												style={{
													height: 300,
													backgroundImage:
														"url(https://www.kivalitaconsulting.com/wp-content/painBackground.svg)",
													backgroundRepeat: "no-repeat",
													backgroundSize: 600,
												}}
											>
												<div
													style={{
														fontFamily: "Montserrat,sans-serif",
														fontSize: 30,
														color: "#115964",
														fontWeight: "bold",
														top: "30%",
														position: "relative",
														backgroundColor: "#ffffffc9",
														width: "40%",
														left: "35%",
													}}
												>
													Precisando diminuir retrabalhos custos e desperdícios
													no seu processo?
												</div>
											</div>
										)}
									</Fragment>
								)}
							</Media>
						</div>
						<hr />
					</Description>
				</Container>
			</section>
		</div>
	)
}
