import Service from "./service"
import TokenService from "./token-service"

interface IReponse {
	accessToken: string
	expirationDate: string
	refreshToken: string
	user: object
}

class AuthService {
	private api = new Service()

	login(email: string, password: string) {
		return this.api
			.post<IReponse>("user/auth", {
				email,
				password,
				grantType: "password",
			})
			.then(response => {
				const { accessToken, refreshToken, user } = response

				TokenService.storeToken(accessToken)
				TokenService.storeRefreshToken(refreshToken)
				TokenService.storeUser(user)

				return response
			})
	}

	logout() {
		TokenService.clearToken()
	}
}

export default AuthService
