import React from "react"
import { Container, Description } from "./styles"
import { useTranslation } from "react-i18next"
import { Link } from "gatsby"

export default function sectFour() {
	const { t } = useTranslation()

	const contents = ["Article", "News", "SocialMidia", "Legislations"]

	const __renderContent = () => {
		return (
			<div className="row">
				{contents.map(content => {
					return (
						<Link
							key={content}
							to={t(`Content.Options.${content}.url`)}
							style={{ color: "white", textDecoration: "none" }}
						>
							<div className="col-sm">
								<img
									src={t(`Content.Options.${content}.imageUrl`)}
									width={250}
									height={250}
									style={{ borderRadius: 10 }}
									alt={`Soluções em qualidade - Logo ${t(`Partners.Siemens.imageUrl`)}`}
									className="align-middle"
								/>
								<h4
									style={{
										textAlign: "center",
										justifyContent: "center",
										marginTop: 5,
										color: "#115964",
									}}
								>
									{t(`Content.Options.${content}.title`)}
								</h4>
							</div>
						</Link>
					)
				})}
			</div>
		)
	}

	return (
		<div>
			<section>
				<Container>
					<Description>
						<div className="container">
							<h1>{t(`Content.Title`)}</h1>
							<hr />
							{__renderContent()}
						</div>
					</Description>
				</Container>
			</section>
		</div>
	)
}
