import React, { useState, useEffect, Fragment } from "react"
import { Link } from "gatsby"
import { useTranslation } from "react-i18next"

import Button from "../../../../utils/Button"
import {
	Container,
	ContainerDescription,
	ContainerSubDescription,
} from "./styles"
import SlideWide from "./components/slideWide"
import Media from "react-media"
import SlideSmall from "./components/slideSmall"

export default function SectOne() {
	const { t } = useTranslation()
	const [loadingImages, setLoadingImages] = useState(true)

	useEffect(() => {
		// Used to wait for image translation loading
		setTimeout(() => {
			setLoadingImages(false)
		}, 300)
	}, [])

	const slide = [
		{ name: "GapAnalysis", isActive: true },
		{ name: "ValidacaoSistemas", isActive: false },
		{ name: "MaoObraEspecializada", isActive: false },
		{ name: "Auditorias", isActive: false },
		{ name: "ProjetosLaboratoriais", isActive: false },
		{ name: "SistemaQualidade", isActive: false },
	]

	const __renderCarousel = () => {
		return (
			<div id="carouselIntro" className="carousel slide" data-ride="carousel">
				<div className="carousel-inner">
					{slide.map(item => {
						return (
							<div
								className={
									item.isActive ? "carousel-item active" : "carousel-item"
								}
								data-interval="5000"
								key={item.name + item.isActive}
							>
								<Media
									queries={{
										small: "(max-width: 599px)",
										medium: "(min-width: 600px) and (max-width: 1199px)",
										large: "(min-width: 1200px)",
									}}
								>
									{matches => (
										<Fragment>
											{matches.small && <SlideSmall name={item.name} />}
											{matches.medium && <SlideWide name={item.name} />}
											{matches.large && <SlideWide name={item.name} />}
										</Fragment>
									)}
								</Media>
							</div>
						)
					})}
					<a
						className="carousel-control-prev"
						href="#carouselIntro"
						role="button"
						data-slide="prev"
					>
						<span className="carousel-control-prev-icon" aria-hidden="true" />
						<span className="sr-only">Previous</span>
					</a>
					<a
						className="carousel-control-next"
						href="#carouselIntro"
						role="button"
						data-slide="next"
					>
						<span className="carousel-control-next-icon" aria-hidden="true" />
						<span className="sr-only">Next</span>
					</a>
				</div>
			</div>
		)
	}

	return (
		<div className="">
			<section id="presentation">
				<Container>
					<div className="row">
						<div className="col-md-12 col-lg-12">
							{!loadingImages && __renderCarousel()}
						</div>
					</div>
				</Container>
			</section>
		</div>
	)
}
