import React, { useState, useEffect, useCallback } from "react"
import Swiper from "react-id-swiper"
import "swiper/css/swiper.css"

import ImageService from "../../../../../services/image-service"
import { IImage } from "../../../../../interfaces"
import convertImageToBase64 from "../../../../utils/convertImageToBase64"
import UserLogged from "../../../../utils/UserLogged"
import { useAuth } from "../../../../../hooks/auth"

import {
	Description,
	ButtonUpload,
	ButtonAlter,
	ModalTitle,
	Button,
} from "./styles"

import { translate } from "../../../../utils/translate"
import { Modal } from "react-responsive-modal"

export default function ClientSection() {
	const imageService = new ImageService()
	const { user } = useAuth()
	const [images, setImages] = useState<IImage[]>([])
	const [selectedImage, setSelectesImage] = useState<IImage>(null)
	const [showModal, setShowModal] = useState(false)
	const [imageToDelete, setImageToDelete] = useState(0)

	const [paramsSwiper, setParamsSwiper] = useState<object>({
		grabCursor: false,
		centeredSlides: true,
		slidesPerView: 3,
		autoplay: true,
		spaceBetween: 50,
	})

	const setSlidesPerView = () => {
		if (window != undefined) {
			if (window.innerWidth < 992) {
				setParamsSwiper(Object.assign(paramsSwiper, { slidesPerView: 1 }))
			} else {
				setParamsSwiper(Object.assign(paramsSwiper, { slidesPerView: 3 }))
			}
		}
	}

	useEffect(() => {
		setSlidesPerView()

		imageService.list<IImage[]>("Partner").then(imageList => {
			setImages(imageList)
		})
	}, [])

	const insertImage = useCallback(async (event: any) => {
		const file = event.target.files[0]

		convertImageToBase64(file).then(async base64 => {
			await imageService.create({
				imageString: base64,
				type: "Partner",
				userId: user.id,
			})
		})
	}, [])

	const updateImage = async (event: any) => {
		const file = event.target.files[0]
		convertImageToBase64(file).then(async base64 => {
			selectedImage.imageString = base64
			await imageService.update(selectedImage)
		})
	}

	const deleteImage = async (imageId: number) => {
		await imageService.delete(imageId)
		const newImages = images.filter(image => image.id !== imageId)
		setImages(newImages)
		setShowModal(false)
	}

	return (
		<div style={{ backgroundColor: "#115964" }}>
			<div className="container" style={{ paddingRight: 0, paddingLeft: 0 }}>
				{showModal && (
					<Modal open={showModal} onClose={() => setShowModal(false)} center>
						<ModalTitle>Tem certeza que deseja deletar esta imagem?</ModalTitle>
						<Button
							onClick={async () => {
								await deleteImage(imageToDelete)
							}}
						>
							Tenho certeza
						</Button>
					</Modal>
				)}
				<section id="our-work">
					<div className="row">
						<div className="col-sm">
							<Description>
								<h3 style={{ color: "white" }}>
									{translate("home.Clients.title")}
								</h3>
								<hr />
							</Description>
						</div>
					</div>
					<div className="row" style={{ justifyContent: "center", padding: 0 }}>
						<div
							className="col-lg-4 col-12"
							style={{
								textAlign: "center",
							}}
						>
							<div
								className="row"
								style={{
									textAlign: "center",
									backgroundColor: "white",
									paddingTop: 10,
									padding: 10,
									justifyContent: "center",
									borderRadius: 50,
									marginBottom: 20,
									marginRight: 5,
									maxHeight: 80,
								}}
							>
								<img
									src="https://www.kivalitaconsulting.com/wp-content/Cliente.png"
									style={{
										width: 60,
										height: 60,
									}}
									alt="Soluções em Qualidade"
								/>
								<p style={{ fontSize: 20 }} className="col-6">
									{translate("home.Clients.clientsTotal")}
								</p>
							</div>
						</div>
						<div
							className="col-lg-4 col-12"
							style={{
								textAlign: "center",
							}}
						>
							<div
								className="row"
								style={{
									textAlign: "center",
									backgroundColor: "white",
									padding: 10,
									justifyContent: "center",
									borderRadius: 50,
									marginBottom: 20,
									marginRight: 10,
									maxHeight: 80,
								}}
							>
								<img
									src="https://www.kivalitaconsulting.com/wp-content/Empresa.png"
									style={{
										width: 60,
										height: 60,
									}}
								/>
								<p style={{ fontSize: 20 }} className="col-6">
									{translate("home.Clients.expertise")}
								</p>
							</div>
						</div>
					</div>
					<div className="row">
						<div className="col-sm" style={{ paddingRight: 0, paddingLeft: 0 }}>
							<div style={{ marginBottom: "32px" }}>
								<UserLogged>
									<ButtonUpload htmlFor="file">Adicionar Imagem</ButtonUpload>
									<input
										type="file"
										name="file"
										id="file"
										hidden
										onChange={insertImage}
										style={{ color: "white" }}
									/>
								</UserLogged>
								{images.length > 0 && (
									<Swiper {...paramsSwiper}>
										{images.map(inImage => (
											<div key={inImage.fileName} data-lightbox="about">
												<div
													style={{
														height: "200px",
														width: "350px",
														background: "no-repeat 50%",
														backgroundSize: "contain",
														backgroundImage: `url(${inImage.imageUrl})`,
														backgroundColor: "white",
														borderRadius: 20,
													}}
												/>
												<div className="row">
													<div className="col-6">
														<UserLogged>
															<ButtonUpload
																htmlFor="NewImage"
																onClick={() => {
																	setSelectesImage(inImage)
																}}
															>
																Alterar Imagem
															</ButtonUpload>
															<input
																type="file"
																name="NewImage"
																id="NewImage"
																hidden
																onChange={event => updateImage(event)}
															/>
														</UserLogged>
													</div>
													<div className="col-6">
														<UserLogged>
															<ButtonUpload
																style={{
																	borderColor: "#fc8383",
																	color: "#fc8383",
																}}
																onClick={() => {
																	setImageToDelete(inImage.id)
																	setShowModal(true)
																}}
															>
																Deletar Imagem
															</ButtonUpload>
														</UserLogged>
													</div>
												</div>
											</div>
										))}
									</Swiper>
								)}
							</div>
						</div>
					</div>
				</section>
			</div>
		</div>
	)
}
