import styled from "styled-components"

export const ButtonUpload = styled.label`
	display: flex;
	max-width: 400px;
	width: 100%;
	margin: 16px auto;
	border: 2px dashed white;
	color: white;
	cursor: pointer;
	justify-content: center;
	align-content: center;
	align-items: center;
	padding: 24px 16px;
	user-select: none;

	svg {
		margin: 0 16px;
	}
`

export const ButtonAlter = styled.label`
	display: flex;
	max-width: 400px;
	width: 50%;
	margin: 16px auto;
	border: 2px dashed white;
	color: white;
	cursor: pointer;
	justify-content: center;
	align-content: center;
	align-items: center;
	padding: 24px 16px;
	user-select: none;

	svg {
		margin: 0 16px;
	}
`

export const Container = styled.div`
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: row;
	margin-bottom: 90px;
`

export const Description = styled.div`
	display: flex;
	justify-content: start;
	align-items: start;
	flex-direction: column;

	margin-bottom: 15px;
	letter-spacing: 7px;
	font-size: 12px;
	font-weight: 300;
	font-family: "Montserrat", sans-serif;
	color: #444;

	p {
		font-size: 16px;
		letter-spacing: 0ch;
		text-align: center;
	}

	h3 {
		margin-top: 20px;
		font-size: 35px;
		font-weight: 600;
		letter-spacing: 0ch;
		color: #444;
	}

	hr {
		border: 1px solid #f4bf8c;
		width: 50px;
		margin: 15px 0px 30px 0px;
	}
`

export const ModalTitle = styled.h2`
	color: #444;
	font-family: "Montserrat", Sans-serif;
	font-size: 25px;
	line-height: 1.3em;
	text-align: center;
	margin: 16px;
`

export const Button = styled.button`
	width: 100%;
	font-size: 17px;
	font-weight: 600;
	color: #fff;
	background: #19b6fd;
	border: 0;
	border-radius: 5px;
	padding: none;
	height: 60px;
	margin-top: 5px;
	outline: 0 !important;
`
