import React from "react"

import Layout from "../components/Layout"

import HomeComponent from "../components/pages/Home"
import SEO from "../components/utils/SEO"
import { AuthProvider } from "../hooks/auth"

const Home = () => {
	return (
		<Layout breadCrumbList={[]}>
			<SEO
				title="Soluções em Qualidade"
				description="Buscando soluções em qualidade? A Kivalita Consulting pode te ajudar! Conheça os nossos serviços e cases de sucesso para atender o seu negócio."
			></SEO>
			<div>
				<AuthProvider>
					<HomeComponent />
				</AuthProvider>
			</div>
		</Layout>
	)
}

export default Home
