import axios from "axios"
import TokenService from "./token-service"

import * as base from "./base-url.js"

interface IResponse<T> {
	statusCode: number
	isStatusCodeSuccess: boolean
	data: T
	errorMessage: string
}

interface IAuth {
	accessToken: string
	expirationDate: string
	refreshToken: string
	Email: string
	Password: string
	GrantType: string
}

class Service<T> {
	isRefreshing = false
	refreshSubscribers = []

	api = axios.create({
		baseURL: base.BaseUrl,
	})

	constructor() {
		this.api.interceptors.request.use(request => {
			request.headers["Authorization"] = `Bearer ${TokenService.getToken()}`
			return request
		})
		this.api.interceptors.response.use(
			response => {
				return response
			},
			error => {
				if (error.response?.status !== 401) {
					return new Promise((resolve, reject) => {
						reject(error)
					})
				}

				return this.getNewToken()
					.then((token: string) => {
						const config = error.config
						config.headers["Authorization"] = `Bearer ${token}`

						return new Promise((resolve, reject) => {
							axios
								.request(config)
								.then(response => {
									resolve(response)
								})
								.catch(error => {
									reject(error)
								})
						})
					})
					.catch((error: any) => {
						Promise.reject(error)
					})
			}
		)
	}

	getNewToken() {
		const auth: IAuth = {
			accessToken: "",
			expirationDate: "",
			refreshToken: TokenService.getRefreshToken() || "",
			Email: "",
			Password: "",
			GrantType: "refresh_token",
		}

		return this.post<IAuth>("User/auth", auth, {}).then(resp => {
			TokenService.storeToken(resp.accessToken)
			TokenService.storeRefreshToken(resp.refreshToken)
			return resp.accessToken
		})
	}

	async get<T>(url: string, headers?: {}) {
		return await this.api
			.get<IResponse<T>>(url, {
				headers: headers,
			})
			.then(response => {
				if (response.data.isStatusCodeSuccess) {
					return response.data.data
				} else {
					throw response.data.errorMessage
				}
			})
	}

	async put<T>(url: string, data: T, headers?: {}) {
		return await this.api
			.put<IResponse<T>>(url, data, {
				headers: headers,
			})
			.then(response => {
				if (response.data.isStatusCodeSuccess) {
					return response.data.data
				} else {
					throw response.data.errorMessage
				}
			})
	}

	async post<T>(url: string, data: object, headers?: {}) {
		return await this.api
			.post<IResponse<T>>(url, data, {
				headers: headers,
			})
			.then(response => {
				if (response.data.isStatusCodeSuccess) {
					return response.data.data
				} else {
					throw response.data.errorMessage
				}
			})
	}

	async delete<T>(url: string, headers?: {}) {
		return await this.api
			.delete<IResponse<T>>(url, {
				headers: headers,
			})
			.then(response => {
				if (response.data.isStatusCodeSuccess) {
					return response.data.data
				} else {
					throw response.data.errorMessage
				}
			})
	}
}

export default Service
