import styled from "styled-components"

export const Container = styled.div`
	margin-top: 48px;

	iframe {
		border-radius: 8px;
		height: 300px;
	}

	@media (max-width: 992px) {
		iframe {
			height: 360px;
		}
	}

	@media (max-width: 576px) {
		iframe {
			height: 220px;
		}
	}
`
export const ButtonRedir = styled.div`
	width: 100%;
	text-align: center;
	margin-top: 8px;
`

export const Description = styled.div`
	margin-bottom: 15px;
	margin-top: 15px;
	letter-spacing: 0;
	font-weight: 300;
	font-size: 12px;
	color: #444;
	font-family: "Montserrat", sans-serif;
	text-align: center;
	justify-content: center;

	h2 {
		font-weight: 300;
		font-size: 16px;
		letter-spacing: 0ch;
		margin-bottom: 32px;
		font-family: "Montserrat", sans-serif;

		@media (max-width: 576px) {
			margin-bottom: 0;
		}
	}

	h3 {
		font-size: 35px;
		font-weight: 600;
		letter-spacing: 0ch;
		color: #444;
		font-family: "Montserrat", sans-serif;
	}

	hr {
		border: 1px solid #f4bf8c;
		width: 200px;
		margin: 30px 0px 30px 0px;
		text-align: center;
	}

	li {
		letter-spacing: 0;
		list-style-type: none;
		font-family: "Montserrat", sans-serif;
		font-size: 17px;
		font-weight: 300;
		margin-bottom: 8px;
		text-align: left;
		justify-content: start;

		span {
			color: #115964;
			margin-left: 8px;
		}

		svg {
			color: #115964;
		}
	}

	@media (max-width: 992px) {
		margin-top: 48px;
		text-align: center;

		hr {
			text-align: center;
			margin: 30px auto;
		}
	}
`
export const Footer = styled.div`
	text-align: center;
	justify-content: center;
	padding-top: 20px;
	font-family: "Montserrat", sans-serif;

	p {
		font-size 20px;
		font-family: "Montserrat", sans-serif;
		text-align: center;
		justify-content: center;
		font-weight: 500;
	}
`
